import { ref } from 'vue';
export default {
  setup() {
    const dialogVisible = ref(false);
    const addDialogVisible = ref(false);

    const handleClose = done => {
      ElMessageBox.confirm('确定关闭对话框?', '温馨提示', {
        type: 'info',
        center: true
      }).then(() => {
        done();
      });
    };

    return {
      dialogVisible,
      handleClose,
      addDialogVisible
    };
  },

  data() {
    return {
      // 分页
      currentPage: 1,
      pagesize: 10,
      // 搜索框
      search: "",
      // 院系所信息
      YxsList: [],
      YxsOption: "选择学院",
      // 选择的院系所id
      YxsId: "",
      YxsMc: "",
      // 实验室信息
      SysList: [],
      // 实验室信息 修改实验室信息使用
      SysInfo: {
        sysh: "",
        // 实验室号
        sysmc: "",
        // 实验室名称
        sysmph: "",
        // 实验室门牌号
        systype: "",
        yxsh: "",
        // 院系所号
        yxmc: "" // 院系所名称

      },
      // 添加实验室 对话框
      activeName: "first",
      // 添加实验室
      addSysInfo: {
        sysmc: "",
        // 实验室名称
        sysmph: "",
        // 实验室门牌号
        capacity: "",
        // 实验室的人数
        systype: '0' // 实验室的状态

      },
      // 设备列表
      sbList: [],
      // 添加设备
      addSbInfo: {
        sysh: "",
        // 实验室号
        sbmc: "",
        // 设备名称
        sbzt: '0' // 设备 状态

      }
    };
  },

  created() {
    this.getYxsList();
  },

  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },

    clearSysInfo() {
      this.SysInfo = {
        sysh: "",
        sysmc: "",
        sysmph: "",
        yxsh: "",
        yxmc: "",
        capacity: "",
        systype: 0
      };
    },

    claearAddSbInfo() {
      this.addSbInfo = {
        sysh: "",
        sbmc: "",
        sbzt: "0"
      };
    },

    // 重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.clearSysInfo();
      this.claearAddSbInfo();
    },

    // 分页
    handleSizeChange: function (size) {
      this.pagesize = size;
    },
    handleCurrentChange: function (currentPage) {
      this.currentPage = currentPage;
    },

    // 获取所有院系所信息
    getYxsList() {
      this.$axios.get("/weixin-yxs/getAllYxs").then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.YxsList = data.data;
        }
      });
    },

    // 选择院系所
    changeYxs(item) {
      if (item.dwh) {
        this.YxsOption = item.dwmc;
        this.YxsId = item.dwh;
        this.YxsMc = item.dwmc;
        Loading.show();
        this.getSysListById(item.dwh);
        Loading.hide();
      } else {
        ElMessage.error("请选择学院");
      }
    },

    // 根据id获取实验室列表
    getSysListById(id) {
      this.$axios.get("/weixin-syszk/getById?id=" + id).then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.SysList = data.data;
        }
      });
    },

    // 删除实验室
    deleteSysById(id) {
      this.$axios.post("/weixin-syszk/deleteById?id=" + id).then(res => {
        const data = res.data;

        if (data.code == 200) {
          ElMessage.success("删除成功");
          Loading.show();
          this.getSysListById(this.YxsId);
          Loading.hide();
        } else {
          ElMessage.error("删除错误");
        }
      });
    },

    // 修改实验室信息
    changeSysInfo(row) {
      this.SysInfo = null;
      this.dialogVisible = true;
      console.log(JSON.parse(JSON.stringify(row)));
      this.SysInfo = JSON.parse(JSON.stringify(row)); // 刷新 设备列表

      Loading.show();
      this.devicelist(this.SysInfo.sysh);
      Loading.hide();
    },

    // 更新实验室信息接口
    updateSysInfo(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$axios.post("/weixin-syszk/updateSysInfo", {
            sysh: this.SysInfo.sysh,
            sysmc: this.SysInfo.sysmc,
            sysmph: this.SysInfo.sysmph,
            capacity: this.SysInfo.capacity,
            systype: this.SysInfo.systype
          }).then(res => {
            const data = res.data;

            if (data.code == 200) {
              ElMessage.success("更新数据成功");
              this.dialogVisible = false;
              this.getSysListById(this.YxsId);
            }
          });
        } else {
          ElMessage.error("表单不合法！！");
        }
      });
    },

    // 添加实验室 对话框
    addSysDialog() {
      if (!this.YxsId) {
        ElMessage.error("请选择学院,如果旁边选择框没有选项,请刷新页面");
        return false;
      }

      this.addDialogVisible = true;
    },

    // 单个添加 实验室
    addSys(formName) {
      // 参数校验
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$axios.post("/weixin-syszk/addSys", {
            sysmc: this.addSysInfo.sysmc,
            sysmph: this.addSysInfo.sysmph,
            capacity: this.addSysInfo.capacity,
            systype: this.addSysInfo.systype,
            yxsh: this.YxsId,
            yxmc: this.YxsMc
          }).then(res => {
            const data = res.data;

            if (data.code == 200) {
              ElMessage.success("添加成功"); // 刷新实验室 

              Loading.show();
              this.getSysListById(this.YxsId);
              Loading.hide();
              this.addDialogVisible = false;
            } else {
              ElMessage.error(data.msg);
            }
          });
        }
      });
    },

    // 上传csv文件批量上传 文件
    uploadCSVFile(params) {
      const file = params.file;
      let formData = new FormData();
      formData.append("file", file);
      formData.append("YxsId", this.YxsId);
      formData.append("YxsMc", this.YxsMc);
      console.log(formData);
      this.$axios({
        url: "/weixin-syszk/addSYSList",
        method: "post",
        data: formData,
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(res => {
        const data = res.data;

        if (data.code == 200) {
          ElMessage.success("添加成功");
          location.reload();
        } else {
          ElMessage.error(data.msg);
        }
      });
    },

    // 添加设备
    addSb() {
      if (this.SysInfo.sysh != "") {
        this.addSbInfo.sysh = this.SysInfo.sysh;
        Loading.show();
        this.addDevice();
        Loading.hide();
        this.claearAddSbInfo();
      }
    },

    // 获取 设备列表
    devicelist(id) {
      this.$axios.post("/weixin-sbzk/deviceList", {
        sysh: id
      }).then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.sbList = data.data;
        } else {
          ElMessage.error("获取设备列表失败");
        }
      });
    },

    // 删除设备
    deleteDevice(id) {
      this.$axios.post("/weixin-sbzk/deleteDevice", {
        sbh: id
      }).then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.devicelist(this.SysInfo.sysh);
          ElMessage.success("删除成功");
        }
      });
    },

    // 添加设备
    addDevice() {
      if (this.addSbInfo.sbmc != "" && this.addSbInfo.sysh != "" && this.addSbInfo.sbzt != "") {
        this.$axios.post("/weixin-sbzk/addDevice", this.addSbInfo).then(res => {
          const data = res.data;

          if (data.code == 200) {
            ElMessage.success("添加成功");
            this.devicelist(this.SysInfo.sysh);
          }
        });
      } else {
        ElMessage.error("请填写设备信息");
      }
    },

    tabsClick(tab) {}

  },
  // 计算属性
  computed: {
    getSysFilter() {
      return this.SysList.map(t => {
        if (t.systype == 0) {
          t['state'] = "开放";
        } else if (t.systype == 1) {
          t['state'] = "禁用";
        } else {
          t['state'] = "报修";
        }

        return t;
      }).filter(data => !this.search || data.sysmc.includes(this.search.toLowerCase()) || data.sysmph.includes(this.search.toLowerCase()) || data.state.includes(this.search.toLowerCase()));
    },

    sbTableInfo() {
      return this.sbList.map(t => {
        t['state'] = t.sbzt == "0" ? "正常" : "故障";
        return t;
      });
    }

  }
};